
import VueCoreImageUpload from 'vue-core-image-upload'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Previewer from 'vux/src/components/previewer/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
name: 'Mine',
directives: {
TransferDom
},
components: {
VueCoreImageUpload,
Previewer,
XTextarea,
XSwitch,
XInput,
Group,
XButton
},
data () {
return {
newMessage: true,
params: {
postName: '',
postLocation: '',
postLatitude: null,
postLongitude: null,
postDescription: '',
postHeadImage: '',
imagesInfo: []
},
images: [],
previewList: [],
uploadUrl: 'http://zayzsh.zbtdvip.com/api/merchant/good/add/goods/baseInfo',
options: {
getThumbBoundsFn (index) {
let thumbnail = document.querySelectorAll('.previewer-demo-img')[index]
let pageYScroll = window.pageYOffset || document.documentElement.scrollTop
let rect = thumbnail.getBoundingClientRect()
return {x: rect.left, y: rect.top + pageYScroll, w: rect.width}
}
}
}
},
created () {
this.$vux.loading.show({
text: '加载中...'
})
},
mounted () {
let query = this.$route.query
console.log(query)
if (query.params) {
this.$vux.loading.hide()
this.params = query.params
} else {
this.getDate()
}
},
methods: {
getBack () {
this.$router.go(-1)
},

getDate () {
let params = {
merchantId: this.$store.state.merchantId,
page: 1,
pageSize: 10
}
this.$http.fetchGet('/merchant/post/get/main', params).then((res) => {
setTimeout(() => {
this.$vux.loading.hide()
}, 500)
this.params = res.data.data.postDetail
})
},

postData () {
if(this.params.dataStatus == 1) {
this.$vux.toast.show({
text: '正在审核中，请审核后再尝试修改',
position: 'middle',
type: 'warn'
})
return
}
if(!this.params.postName) {
this.$vux.toast.show({
text: '请输入驿站名称',
position: 'middle',
type: 'warn'
})
return
}
this.$vux.loading.show({
text: '加载中...'
})
let params = this.params
console.log(params)
this.$http.fetchPost('/merchant/post/update/merchantInfo', params).then((res) => {
this.$vux.loading.hide()
if (res.data.code === 200) {
this.$vux.toast.show({
text: '保存成功',
position: 'middle'
})
this.$router.go(-1)
} else {
this.$vux.toast.show({
text: res.data.message,
position: 'middle',
type: 'warn'
})
}
})
},

/*imagechanged_avater (data) {
let param = new FormData() 
param.append('files', data)
this.$http.fetchPost('/merchant/common/image/upload', param, {
headers: {
'Content-Type': 'multipart/form-data; boundary=----WebKitFormBoundaryJBcoeGdBCguPERbU'
}
}).then((res) => {
this.params.postHeadImage = res.data.data.path
})
},*/
