window.optionPictures = function(data){
if (data.selectType == 1) {
that.$vux.toast.show({
text: '请上传图片',
position: 'middle',
type: 'warn'
})
} else {
if(data.successType == 1) {
that.params.postHeadImage = data.imagePath
} else {
that.$vux.toast.show({
text: data.info,
position: 'middle',
type: 'warn'
})
}
}
let ua = navigator.userAgent.toLowerCase();

